<template>
	<div>
		<!-- 图片 -->
		<div style="text-align: center;">
			<img class="logo" src="../../static/img/logo.png" />
		</div>

		<!-- 输入框 -->
		<div class="jinput-layout">
			<div>
				{{$t('Current-resonance-ratio')}} 300U : {{ratioEco}}ECO
			</div>
			<div class="j-input-layout">
				<div class="j-2">
					<input v-model="inputUsdt" class="j-input" @keyup="change1"></input>
					<span class="j-usdt">USDT</span>
				</div>
				<img class="j-img" src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABMAAAAYBAMAAADquds8AAAAHlBMVEUAAAAzMzMxMTEuLi4zMzM0NDQzMzMzMzMzMzMzMzOQsOS/AAAACXRSTlMAFBUWGXrDxNMhK/qpAAAAQ0lEQVQY02NgAAKWQAYYYJ2hAGMydjbBhS0QwsxIwhoIYSYUYQGGzJlQkIDMJAUgaatEMCVwWUlYuBEhPBXOZAwBUwCRdiP2PElMOAAAAABJRU5ErkJggg==">
				<div class="j-2">
					<input v-model="inputEco" class="j-input" @keyup="change2"/>
					<span class="j-usdt">ECO</span>
				</div>
				<div v-if="isApproveUsdt" class="j-approve all-pointer" @click="dealUsdt()">{{$t('Send-out')}}</div>
				<div v-else style="background: #FFB400;" class="j-approve all-pointer" @click="approveUsdt()">{{$t('Approval')}}</div>
				
			</div>
			<div style="color: #808080; font-size: 15px;">
				{{$t('Current-currency-price')}}: {{ratioUsdt}} USDT
			</div>
		</div>

		<!-- 折线图 -->
		<div class="all-w2">
			<div id="line-chart" style="width: 100%;height:400px;"></div>
		</div>

		<!-- 剩下全部 -->
		<!-- 第二块 -->
		<div class="all-w">
			<div class="all-right-two">
				<div>{{e2.e1}}</div>
				<div>{{$t('Eco-Holdings')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{e2.e2}}</div>
				<div>{{$t('Eco-liquidity-mining-revenue')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{e2.e3}}</div>
				<div>{{$t('Withdrawal-quantity')}} <span @click="e21()" class="all-btn-2">{{$t('Extract')}}</span></div>
			</div>
		</div>

		<!-- 第三块 -->
		<div class="all-w">
			<div class="all-right-two">
				<div>{{e3.e1}}</div>
				<div>{{$t('Eid-Activation')}} <span @click="e31"class="all-btn-2">{{$t('Activation')}}</span></div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{e3.e2}}</div>
				<div>{{$t('Eid-invitation-link')}} <span @click="e32"class="all-btn-2">{{$t('Copy')}}</span></div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{e3.e3}}</div>
				<div>{{$t('My-inviting-address')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{e3.e4}}</div>
				<div>{{$t('Eid-resonance-benefits')}} <span @click="e33" class="all-btn-2">{{$t('Withdrawal')}}</span></div>
			</div>
		</div>

		<!-- 第四块 -->
		<div class="all-w">
			<div class="all-right-two">
				<div>{{e4.e1}}</div>
				<div>{{$t('Expected-income')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{e4.e2}}</div>
				<div>{{$t('Eid-matrix-revenue')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{e4.e3}}</div>
				<div>{{$t('Node-dividend-income')}}</div>
			</div>
			<div class="all-hr"></div>
			<span @click="e41()" class="all-btn all-pointer" style="
			width: 50%; margin: 0 auto; border-radius: 5px; padding: 8px 0;
			">{{$t('Withdrawal')}}</span>
		</div>

		<!-- 第五块 -->
		<div class="all-w">
			<div class="v5-layout">
				<div class="v5-1">
					<div :class="e5.e1===1?'v52':'v5'">E1</div>
					<div :class="e5.e1===2?'v52':'v5'">E2</div>
					<div :class="e5.e1===3?'v52':'v5'">E3</div>
					<div :class="e5.e1===4?'v52':'v5'">E4</div>
					<div :class="e5.e1===5?'v52':'v5'">E5</div>
				</div>
				<div class="v5-2">
					<div style="display: flex; flex-direction: column; width: 100%;">
						<input type="number" class="v5-input" v-model="e5.e2" :placeholder="$t('Input-mortgage-quantity')" />
						<div class="v5-u">
								<div :class="e5.e6===1?'v5-u-2': 'v5-u-1'" @click="selectAmount(1)">3000</div>
								<div :class="e5.e6===2?'v5-u-2': 'v5-u-1'" @click="selectAmount(2)">5000</div>
								<div :class="e5.e6===3?'v5-u-2': 'v5-u-1'" @click="selectAmount(3)">10000</div>
								<div :class="e5.e6===4?'v5-u-2': 'v5-u-1'" @click="selectAmount(4)">15000</div>
								<div :class="e5.e6===5?'v5-u-2': 'v5-u-1'" @click="selectAmount(5)">20000</div>
								<div :class="e5.e6===6?'v5-u-2': 'v5-u-1'" @click="selectAmount(6)">25000</div>
						</div>
						<div style="text-align: right; margin-top: 10px;">{{$t('Class-E-node')}} <span class="all-btn-2" @click="e51()">{{$t('Stake')}}</span></div>
					</div>
				</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{e5.e3}}</div>
				<div>{{$t('Total-activated')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{e5.e4}}</div>
				<div>{{$t('Countdown-to-divestment')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{e5.e5}}</div>
				<div>{{$t('Staked')}} <span class="all-btn-2" @click="e52()">{{$t('Divestment')}}</span></div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				/* 其它 */
				// 判断当前地址有没有授权给usdt合约给到eco合约
				isApproveUsdt: false,
				
				/* 第一块-输入框和 */
				// 共振比列ECO
				ratioEco: '0',
				// 输入框usdt
				inputUsdt: '',
				// 输入框eco
				inputEco: '',
				// 当前币价usdt
				ratioUsdt: '0',
				/* 折线图 */
				
				
				/* 第二块-ECO持有量 */
				e2: {
					// eco持有量
					e1: '-',
					// eco流动性挖矿收益
					e2: '-',
					// 可提现数量
					e3: '-',
				},
				
				/* 第三块-EID激活 */
				e3: {
					// 默认1000个
					e1: '1000.000',
					e2: '-',
					// 复制的完整地址
					e22: '',
					e3: '-',
					e4: '-',
				},
				
				/* 第四块-预计可提现收益 */
				e4: {
					e1: '-',
					e2: '-',
					e3: '-',
				},
				
				/* 第五块-E级节点 */
				e5: {
					// 区别级别的颜色
					e1: '-',
					// 输入框的值
					e2: '',
					// 统计激活人数
					e3: '-',
					// 可撤资倒计时
					e4: '00:00:00',
					/// 已质押
					e5: '-',
					// 区别数量的颜色
					e6: 0,
				},
				
			}
		},
		
		async created() {
			// 同步锁
			await this.$onLaunched;
			this.isApprove();
			this.getE1();
			this.getE2();
			this.getE3();
			this.getE4();
			this.getE5();
		},
		
		mounted() {
			this.drawChart();
		},

		methods: {
			// 判断有没有授权usdt给eco门票合约, 显示不同的按钮
			async isApprove() {
				let res = await this.$contract.allowance();
				res = this.$util.div10e6(res);
				if(res > 1) {
					this.isApproveUsdt = true;
				}else {
					this.isApproveUsdt = false;
				}
			},
			// 用户批准usdt给eco门票合约; -交易
			async approveUsdt() {
				let res = await this.$contract.approveTickets();
				await isApprove();
			}, 
			// 用户使用usdt兑换eco; - 交易
			async dealUsdt() {
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				let referrer = await this.$contract.userData(address);
				referrer = await this.$contract.addressFromHex(referrer.referrer);
				console.log(referrer);
				// 使用usdt购买eco
				let value = this.$util.mul10e6(this.inputUsdt);
				if(value <= 0) {
					return;
				}
				let res = await this.$contract.vending(referrer, value);
			},
			
			// 画图
			drawChart() {
				let myChart = echarts.init(document.getElementById('line-chart'));
				// 指定图表的配置项和数据
				let base = +new Date(1968, 9, 3);
				var oneDay = 24 * 3600 * 1000;
				var date = [];
				var data = [Math.random() * 300];
				let o = 1;
				for (var i = 1; i < 20000; i++) {
					var now = new Date(base += oneDay);
					date.push([now.getFullYear(), now.getMonth() + 1, now.getDate()].join('/'));
					data.push(Math.round((Math.random() +0.1) * 20 + data[i - 1]));
				};
				
				let option = {
					tooltip: {
					        trigger: 'axis'
					    },
					/* tooltip: {
						trigger: 'axis',
						position: function(pt) {
							return [pt[0], '10%'];
						}
					}, */
					/* title: {
						left: 'center',
						text: '大数据量面积图',
					},
					toolbox: {
						feature: {
							dataZoom: {
								yAxisIndex: 'none'
							},
							restore: {},
							saveAsImage: {}
						}
					}, */
					xAxis: {
						type: 'category',
						boundaryGap: false,
						data: date
					},
					yAxis: {
						type: 'value',
						boundaryGap: [0, '100%']
					},
					dataZoom: [{
						// x是竖的, y是横的, 0-10是y的长度
						type: 'inside',
						start: 0,
						end: 10
					}, {
						start: 0,
						end: 10,
						handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
						handleSize: '80%',
						handleStyle: {
							color: '#fff',
							shadowBlur: 3,
							shadowColor: 'rgba(0, 0, 0, 0.6)',
							shadowOffsetX: 2,
							shadowOffsetY: 2
						}
					}],
					series: [{
						name: '模拟数据',
						type: 'line',
						smooth: true,
						symbol: 'none',
						sampling: 'average',
						itemStyle: {
							color: 'rgb(255, 70, 131)'
						},
						areaStyle: {
							color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
								offset: 0,
								color: 'rgb(255, 158, 68)'
							}, {
								offset: 1,
								color: 'rgb(255, 70, 131)'
							}])
						},
						data: data
					}]
				};
				// 使用刚指定的配置项和数据显示图表。
				myChart.setOption(option);
			},
			
			/* 第一块-输入框 */
			async getE1() {
				let res = await this.$contract.price();
				res = this.$util.numberFormat(this.$util.div10e18(this.$util.hexToDec(res._hex)*300));
				this.ratioEco = res;
				this.ratioUsdt = this.$util.numberFormat(300 / res)
			},
			// 输入框数据发生变化
			change1() {
				this.inputEco = this.$util.numberFormat(this.inputUsdt / this.ratioUsdt);
			},
			change2() {
				this.inputUsdt = this.$util.numberFormat(this.inputEco * this.ratioUsdt);
			},
			
			/* 第二块-ECO持有量 */
			async getE2() {
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				let res = await this.$contract.getEcoBalance(address);
				let res2 = await this.$contract.calcLMineIncome(address);
				let res3 = await this.$contract.withdrawalAmount(address);
				let e = {
					e1: this.$util.numberFormat(this.$util.div10e18(this.$util.hexToDec(res._hex))),
					e2: this.$util.numberFormat(this.$util.div10e18(this.$util.hexToDec(res2._hex))),
					e3: this.$util.numberFormat(this.$util.div10e18(this.$util.hexToDec(res3._hex))),
				}
				this.e2 = e;
			},
			// 点击了可提取数量 - 提取
			async e21() {
				let res = await this.$contract.takeUnlockIncome();
				await this.getE2();
				console.log('点击了可提取数量 - 提现');
			},
			
			/* 第三款-ECO持有量 */
			async getE3() {
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				let res2 = await this.$contract.userReferrer(address);
				//this.v2.referrer = this.$util.addressCurtail(await this.$contract.addressFromHex(res2.referrer));
				let res3 = await this.$contract.eidExIncome(address);
				this.e3.e2 = this.$util.addressCurtail(address);
				this.e3.e22 = address;
				this.e3.e3 = this.$util.addressCurtail(await this.$contract.addressFromHex(res2.referrer));
				this.e3.e4 = this.$util.numberFormat(this.$util.div10e18(this.$util.hexToDec(res3._hex)));
			},
			// 点击了EID激活 - 激活
			async e31() {
				let res = await this.$contract.activeEid();
				await this.getE3();
				console.log('点击了EID激活 - 激活', res)
			},
			// 点击了复制EID邀请链接 - 复制
			async e32() {
				console.log(' 点击了复制EID邀请链接 - 复制');
				let value = this.e3.e22;
				value = window.location.origin + '?referrer=' + value;
				let input = document.createElement("input");
				input.value = value;
				document.body.appendChild(input);
				input.select();
				input.setSelectionRange(0, input.value.length), document.execCommand('Copy');
				document.body.removeChild(input);
			},
			// 点击了EID共振收益 - 提现
			async e33() {
				let res = await this.$contract.takeEidExIncome();
				await this.getE3();
				console.log('点击了EID共振收益 - 提现', res);
			},
			
			/* 第四块-预计可提现收益 */
			async getE4() {
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				let res = await this.$contract.expectedIncome(address);
				let res2 = await this.$contract.eidMatIncome(address);
				let res3 = await this.$contract.eidNodeIncome(address);
				this.e4.e1 = this.$util.numberFormat(this.$util.div10e18(this.$util.hexToDec(res._hex)));
				this.e4.e2 = this.$util.numberFormat(this.$util.div10e18(this.$util.hexToDec(res2._hex)));
				this.e4.e3 = this.$util.numberFormat(this.$util.div10e18(this.$util.hexToDec(res3._hex)));
			},
			// 点击了提现按钮
			async e41() {
				let res = await this.$contract.take();
				await this.getE4();
				console.log('点击了提现按钮', res)
			},
			
			/* 第五块-E级节点 */
			async getE5() {
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				// 获取节点E1...
				let res = await this.$contract.eidNodes(address);
				let res2 = await this.$contract.activeEidAmount();
				let res3 = await this.$contract.pledgeEndTime(address);
				let res4 = await this.$contract.pledgeAmount(address);
				this.e5.e1 = res.nodeLevel;
				// 人数
				this.e5.e3 = this.$util.hexToDec(res2._hex);
				// 倒计时
				let downTime = this.$util.hexToDec(res3._hex);
				let test = setInterval(() => {
				  downTime -= 1;
				  this.e5.e4 = this.$util.hoursTime(downTime);
				},1000);
				// 已质押
				this.e5.e5 = this.$util.numberFormat(this.$util.div10e18(this.$util.hexToDec(res4._hex)));
			},
			// 选中质押的数量
			selectAmount(amount) {
				if(amount === 1) {
					this.e5.e6 = 1;
					this.e5.e2 = 3000;
				}else if(amount === 2) {
					this.e5.e6 = 2;
					this.e5.e2 = 5000;
				}else if(amount === 3) {
					this.e5.e6 = 3;
					this.e5.e2 = 1000;
				}else if(amount === 4) {
					this.e5.e6 = 4;
					this.e5.e2 = 15000;
				}else if(amount === 5) {
					this.e5.e6 = 5;
					this.e5.e2 = 20000;
				}else if(amount === 6) {
					this.e5.e6 = 6;
					this.e5.e2 = 25000;
				}else {
					this.e5.e6 = 0;
				}
				console.log('n', amount)
			},
			// 点击了质押
			async e51() {
				// 获取输入框的数据
				let number = this.e5.e2;
				number = this.$util.mul10e18(number);
				let res = await this.$contract.pledgeNode(number);
				console.log('传入数据', number, res)
			},
			// 点击撤资
			async e52() {
				let res = await this.$contract.unPledgeNode();
				console.log('点击了撤资', res)
			},
			
		}

	}
</script>

<style>
	/* 输入框 */
	/* 宽度尺度大于1500时 */
	@media only screen and (min-width: 415px) {
		.jinput-layout {
			padding: 30px 0 0 0;
			width: 70%;
			margin: 0 auto;
			display: flex;
			flex-direction: column;
			text-align: left;
		}

		.jinput-layout>div {
			margin-bottom: 10px;
		}

		/* 输入框 */
		.j-input-layout {
			display: flex;
		}

		.j-2 {
			display: flex;
		}

		.j-input:focus {
			outline: none;
		}

		.j-input {
			padding: 10px 20px;
			font-size: 20px;
			border-radius: 20px 0 0 20px;
			border: 1px solid #F5F5F5;
			height: 30px;
			width: 175px;
		}

		.j-usdt {
			background: #FFFFFF;
			line-height: 50px;
			text-align: center;
			padding: 0 20px;
			background: rgb(250, 250, 250);
			border-radius: 0 20px 20px 0;
		}

		.j-approve {
			background: #34CECC;
			color: #FFFFFF;
			text-align: center;
			margin-left: 20px;
			border-radius: 10px;
			line-height: 50px;
			min-width: 80px;
		}

		.j-img {
			margin: 0 20px;
			width: 150px;
			height: 25px;
			margin-top: 10px;
		}
	}

	/* 宽度尺寸小于414时 */
	@media only screen and (max-width: 414px) {
		.jinput-layout {
			padding: 30px 0 0 0;
			display: flex;
			flex-direction: column;
			text-align: left;
		}

		.jinput-layout>div {
			margin-bottom: 10px;
		}

		/* 输入框 */
		.j-input-layout {
			display: flex;
			flex-direction: column;
		}

		.j-2 {
			display: flex;
		}

		.j-input:focus {
			outline: none;
		}

		.j-input {
			padding: 10px 20px;
			font-size: 20px;
			border-radius: 20px 0 0 20px;
			border: 1px solid #F5F5F5;
			height: 30px;
			width: 70%;
		}

		.j-usdt {
			background: #FFFFFF;
			line-height: 50px;
			text-align: center;
			padding: 0 20px;
			background: rgb(250, 250, 250);
			border-radius: 0 20px 20px 0;
		}

		.j-approve {
			background: #34CECC;
			color: #FFFFFF;
			text-align: center;
			border-radius: 10px;
			line-height: 40px;
			min-width: 80px;
			margin-top: 10px;
		}

		.j-img {
			padding: 10px 0;
