			height: 20px;
			margin: 0 auto;
		}

	}

	/* 折线图和剩下全部 */
	#main {
		height: 300px;
	}

	.all-w2 {
		background: #FFFFFF;
		border: 1px solid #F5F5F5;
		border-radius: 15px;
		margin-bottom: 30px;
	}

	.v5-1 {
