		overflow-y: auto;
	}

	.v5-u-1 {
		border: 1px solid #E0E0E0;
		margin-right: 10px;
		padding: 1px 5px;
		color: #808080;
		cursor: pointer;
	}
	.v5-u-2 {
		border: 1px solid #34CECC;
		margin-right: 10px;
		padding: 1px 5px;
		color: #34CECC;
		cursor: pointer;
	}

	@media only screen and (min-width: 415px) {
		.v5-layout {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			width: 100%;
		}

		.v5-2 {
			width: 100%;
			display: flex;
			justify-content: space-between;
		}

		.v5-input {
			padding: 5px 10px;
			font-size: 15px;
			border: 1px solid #F5F5F5;
			border-radius: 5px;
			height: 25px;
			margin-bottom: 10px;
			width: 330px;
		}
	}

	@media only screen and (max-width: 414px) {
		.v5-layout {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
		}

		.v5-2 {
			width: 100%;
		}

		.v5-input {
			padding: 5px 5px;
			font-size: 15px;
			border: 1px solid #F5F5F5;
			border-radius: 5px;
			height: 25px;
			margin-bottom: 10px;
			text-align: left;

		}

		.v5-2>div:nth-child(2) {
			margin-top: 20px;
			text-align: right;
		}

	}
</style>



